@import "normalize.css";
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;800&display=swap');

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: 'Inter', sans-serif;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}
